<template>
  <div>
    <p class="noWrapper" v-if="!MY_FRIEND_OBJECT_LIST.MY_FRIEND_LIST.length"><img src="../../assets/img/da.png" alt="没有关注的课程哦~"><br>暂时还没有内容~~宝宝先看看别的~~</p>
    <mt-cell-swipe v-for="(item, index) in MY_FRIEND_OBJECT_LIST.MY_FRIEND_LIST" :key="index"
    class="as-Fans"
      :title="item.nickname"
      :label="item.introduction"
      :right="[
        {
          content: item.status ? '取消关注' : '添加关注',
          style: { background: 'red', color: '#fff' },
          handler: () => attentionCancel(item)
        }
      ]">
      <img @click="__enterPeopleCenter(item)" slot="icon" :src="item.avatar">
    </mt-cell-swipe>
  </div>
</template>
<script>
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('userModule')
  export default {
    data() {
      return {
        cancelAttentionChange: 0,
        plane: require('../../assets/img/zhitiao.png'),
        page: 1,
        falgNoData: false,
        pagination: true,
        showData: false,
      }
    },
    methods: {
      ...mapActions([
        'GET_MYFRIEND'
      ]),
      __enterPeopleCenter(item) {
        this.$router.push({
          name: 'perWorkCircle',
          params: {
            id: item.userId
          }
        });
      },
      attentionCancel(item) {
        this.$Indicator.open({
          text: '操作中...',
          spinnerType: 'fading-circle'
        })
        if (item.status) {
          this.axios.post('/api/user-following/delete', {
            followingId: item.userId
          }).then(data => {
            this.$Indicator.close()
            if (data.data.result == '移除成功') {
              this.$set(item, 'status', 0)
              this.$Toast({
                message: '移除成功',
                position: 'middle',
                duration: 1000
              })
            } else if (data.data.result == '移除失败') {
              this.$Toast({
                message: '移除失败',
                position: 'middle',
                duration: 1000
              })
            } else {
              this.$Toast({
                message: '请登录后操作',
                position: 'middle',
                duration: 1000
              })
              this.$router.push('/mine');
            }
          }).catch(err => {
            this.$Indicator.close()
            this.$Toast({
              message: '发生异常,请稍后操作',
              position: 'middle',
              duration: 1000
            })
            console.log(err)
          })
        } else {
          this.axios.post('/api/user-following/add', {
            followingId: item.userId
          }).then(data => {
            this.$Indicator.close()
            if (data.data.result == '关注成功') {
              this.$Toast({
                message: '关注成功',
                position: 'middle',
                duration: 1000
              })
              this.$set(item, 'status', 1);
            } else {
              this.$Toast({
                message: '请登录后操作',
                position: 'middle',
                duration: 1000
              })
              this.$router.push('/mine');
            }
          }).catch(err => {
            this.$Toast({
              message: '发生异常,请稍后操作',
              position: 'middle',
              duration: 1000
            })
            console.log(err)
          })
        }
      },
      dropDown() {
        
        if (this.falgNoData) { //数据全部加载完了
          return;
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.falgNoData = true
          this.GET_MYFRIEND({
            params: {
              page: ++this.$store.state.userModule.MY_FRIEND_OBJECT_LIST.MY_FRIEND_PAGE
            }
          }).then( data => {
            if (this.pagination) {
              if (data.data.contents && data.data.contents.length > 0) {
                this.$Indicator.close()
                this.falgNoData = false
              } else {
                this.$Indicator.close()
                this.$Toast({
                  message: '没有更多了',
                  position: 'middle',
                  duration: 1000
                })
                this.pagination = false
                this.showData = true
                this.falgNoData = true
              }
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
    },
    computed: {
      ...mapState([
        'MY_FRIEND_OBJECT_LIST'
      ])
    },
    mounted() {
      window.onscroll = () => {
        this.dropDown()
      }
      if(this.MY_FRIEND_OBJECT_LIST && this.MY_FRIEND_OBJECT_LIST.MY_FRIEND_LIST.length > 0){
        return
      }
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.GET_MYFRIEND({
        params: {
          page: 1
        }
      }).then( data => {
        this.$Indicator.close()
      }).catch(err => {
        console.log(err)
      })
    },
  }
</script>
<style lang="less">
.mint-cell-label{
  padding-left: 80px;
  display: inline-block;
}
.mint-cell-title{
  &>span{
    &>img{
      width: 70px;
      height: 70px;
      border-radius: 50%;
    }
    &:nth-child(3){
      display: inline-block;
      width: 500px;
      height: 50px;
      overflow: hidden;
      margin-top: 0;
      line-height: 30px;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>

<style lang="less" scoped>
  /* 关注列表 END */
  .noWrapper{
    text-align: center;
    &>img{
      width: 400px;
      margin: 40px 0 20px 0;
    }
    color: #aaa;
    font-size: 30px;
  }
  .as-Fans {
    padding:10px 20px;
  }
</style>


